.DrawerToggleContainer {
  width: 40px;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
  position: relative;
  top: 0;
  left: 0;
}

.DrawerToggle, .ShowClose {
  position: absolute;
  top: 17px;
  left: 0;
  width: 30px;
  height: 30px;
  -webkit-transition: all 300ms cubic-bezier(.61, .01, .42, 1);
  -o-transition: all 300ms cubic-bezier(.61, .01, .42, 1);
  transition: all 300ms cubic-bezier(.61, .01, .42, 1);
  cursor: pointer;
  background: transparent;
  border: 0px;
}

.DrawerToggle div, .ShowClose div {
  height: 0px;
  border: 1.5px solid #fff;
  width: 22px;
  display: block;
  position: absolute;
  -webkit-transition: all 300ms cubic-bezier(.61, .01, .42, 1);
  -o-transition: all 300ms cubic-bezier(.61, .01, .42, 1);
  transition: all 300ms cubic-bezier(.61, .01, .42, 1);
  background:#fff;
}

.DrawerToggle:focus, .ShowClose:focus {
  outline: 0px;
}

.DrawerToggle div:nth-child(1) {
  top: 0px;
}

.DrawerToggle div:nth-child(2) {
  top: 8px;
}

.DrawerToggle div:nth-child(3) {
  top: 16px;
}

.DrawerToggle div:nth-child(3):before {
  opacity: 0;
  content: '';
  border: 2px solid #fff;
  display: block;
  position: relative;
  top: 0.25px;
}

.ShowClose div:nth-child(3):before,
.ShowClose div:nth-child(3):before,
.ShowClose div:nth-child(2) {
  opacity: 0;
  -webkit-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
}

.ShowClose div {
  border: 1.5px solid #fff;
  height: 0px;
  border-radius: 0%;
  margin-left: -1px;
  margin-top: 6px;
  -webkit-animation: division 300ms linear 1;
          animation: division 300ms linear 1;
  width: 25px;
  top: 0px;
}

.ShowClose div:nth-child(2) {
  width: 0px;
}

.ShowClose div:nth-child(3) {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

.ShowClose div:nth-child(1) {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

@-webkit-keyframes division {
  0% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}

@keyframes division {
  0% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}

@media (min-width: 500px) {
  .DrawerToggleContainer {
    display: none;
  }
}


