.Paper {
  width: 200px;
  height: 280px;
  padding: 5px;
  cursor: pointer;
  font-size: 1.2em;
  background-image: -webkit-gradient(linear,left top, left bottom,from(#3b57ca),to(#55a8d4));
  background-image: -webkit-linear-gradient(#3b57ca,#55a8d4);
  background-image: -o-linear-gradient(#3b57ca,#55a8d4);
  background-image: linear-gradient(#3b57ca,#55a8d4);
}

.Paper p {
  margin: 0;
  padding: 0;
  text-align: center;
}

.IconContainer {
  width: 100%;
  text-align: center;
  margin: 20px 0;
  display: inline-block;
}

.IconContainer i {
  font-size: 64px;
  margin-bottom: 20px;
}

.CurrentDate {
  margin: 0;
  font-size: 1.3em;
  text-align: center;
  font-weight: normal;
}


